<template>
  <div class="search-container">
    <!--? 顶部搜索 -->
    <!--? 在van-search外层,添加from标签,且active属性不能为空,即可在ios输入法中显示搜索按钮-->
    <form action="/">
      <van-search
      v-model="searchText"
      shape="round"
      show-action
      background="#3296fa"
      placeholder="请输入搜索关键词"
      @search="onSearch"
      @cancel="onCancel"
      @focus="isResultShow = false"
    />
    </form>
    <!--? 查找结果 -->
    <SearchResult
      v-if="isResultShow"
      :searchText="searchText" />
    <!--? 联想列表 -->
    <SearchSuggestion
      v-else-if="searchText"
      :searchText="searchText"
      @search="onSearch" />
    <!--? 搜索历史 -->
    <!--? 组件中的ref可以让父组件去调用子组件的方法 / 使用子组件的变量 -->
    <SearchHistory
      v-else
      ref="SearchHistory"
      :searchHistories="searchHistories"
      @clearAll="searchHistories = []"
      @clearHistory="onClearHistory"
      @search="onSearch"
      />
  </div>
</template>

<script>

/*

*/
import SearchHistory from './components/SearchHistory.vue'
import SearchSuggestion from './components/SearchSuggestion.vue'
import SearchResult from './components/SearchResult.vue'
// 引入防抖方法

import { getItem, setItem } from '../../utils/storage.js'
export default {
  name: 'search-vue',
  components: {
    SearchHistory,
    SearchSuggestion,
    SearchResult
  },
  data () {
    return {
      searchText: '', // 搜索框文本
      isResultShow: false, // 是否展示搜索结果
      searchHistories: getItem('TOUTIAO_SEARCH_HISTORY') || [] // 搜索记录列表
    }
  },
  watch: {
    searchHistories: {
      handler (newVal) {
        setItem('TOUTIAO_SEARCH_HISTORY', newVal)
      },
      deep: true
    }
  },
  methods: {
    // 搜索事件
    // value: 表示的是输入框中的值
    onSearch (value) {
      // 将当前的内容赋值给输入框
      this.searchText = value
      // 主动开启搜索结果组件
      this.isResultShow = true
      console.log(value)
      // 将搜索框的内容放到搜索历史列表中
      this.onAddTarget(value)
    },
    // 添加历史数据
    onAddTarget (value) {
      /*
        需求:

      */
      // 判断在原数组中有没有该字符串
      const index = this.searchHistories.indexOf(value)
      if (index !== -1) {
        // 列表中没有该元素 - 删除该元素
        this.searchHistories.splice(index, 1)
      }
      // 将value更新到列表的最顶端
      this.searchHistories.unshift(value)
      console.log(this.searchHistories, 'onAddTarget')
    },
    // 关闭事件
    // 返回前一个路由
    onCancel () {
      this.$toast('取消搜索')
      // 返回前一个路由页面
      this.$router.back()
    },
    // 删除单独历史
    onClearHistory (index) {
      this.searchHistories.splice(index, 1)
    }
  }
}
</script>

<style lang="less" scoped>
.search-container {
  padding-top: 108px;
  .van-search__action {
    color: #fff;
  }
   // 将搜索栏置顶， 并设置padding-top来限制文本区域的滚动范围
  .van-search {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
  }
}
</style>
