<template>
  <div class="search-suggestion">
    <van-cell
    v-for="(item,index) in suggestionList"
    :key="index"
    icon="search"
    @click="$emit('search',item)"
    >
      <template #title>
        <span v-html="highlight(item)"></span>
      </template>
    </van-cell>
  </div>
</template>

<script>
import { getSearchSuggestionAPI } from '../../../api/index.js'
import { debounce } from 'lodash'
// 手写防抖
// 仅供拓展，有时间的时候背一背
/*
function myDebounce (callback, times) {
  // 通过闭包延迟时间timer的什么周期
  let timer = null // 计时器
  // 高阶函数， 一个函数返回另一个函数
  return function (val) {
    const that = this
    if (timer) {
      return clearTimeout(timer)
    }

    timer = setTimeout(() => {
      // 通过call改变this指向
      callback.call(that, val)
    }, times)
  }
}
*/
export default {
  name: 'SearchSuggestion',
  props: {
    searchText: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      suggestionList: [] // 联想建议列表数据
    }
  },
  watch: {
    // searchText (value) {
    //   // 输入付一个字符并不会触发监听
    //   console.log(value)
    // }

    searchText: {
      // lodash的防抖函数必须使用普通函数 debounce(function(){}, 连续触发事件)
      handler: debounce(function (newValue) {
        // 调用查询联想列表接口
        this.loadSearchSuggestion(newValue)
      }, 200),
      immediate: true // 首次监听触发
    }
  },
  methods: {
    // 查询数据接口
    async loadSearchSuggestion (newVal) {
      try {
        const { data: res } = await getSearchSuggestionAPI(newVal)
        console.log(res)
        // 调用接口时发现 有些情况下后端会返回一个[null]的情况,针对这种情况,我们可以主动的将这个null改写成当前searchText的值
        if (res.data.options && res.data.options[0] == null) {
          // 判断有无options数组,并且数组的第0项是不是空
          res.data.options[0] = this.searchText
        }
        // 实现高亮文字结构的处理 后 => <span style="color: red">后</span>
        // 方法一: 正则表达式替换 \正则\ + replace
        // const list = res.data.options.map(item => {
        //   // 1.设置关键字高亮文本样式
        //   const highlightStr = `<span style="color: red">${newVal}</span>`
        //   // 2.根据输入框内容创建正则规则
        //   const reg = new RegExp(newVal, 'gi')

        //   // 3. 通过  \正则\ + replace 进行文本替换
        //   return item.replace(reg, highlightStr)
        // })
        // this.suggestionList = list

        // // 方法二 split 与 join 结合使用

        // const list2 = res.data.options.map(item => {
        //   // 1.设置关键字的高亮文本样式
        //   const highlightStr = `<span style="color: red">${newVal}</span>`
        //   // 2.通过split方法将目标字符串拆分成数组
        //   // 3.通过join方法,将highlightStr 拼接到每个元素之间
        //   return item.split(newVal).join(highlightStr)
        // })
        // this.suggestionList = list2
        this.suggestionList = res.data.options
        console.log(this.suggestionList)
      } catch (error) {
        this.$toast('获取列表失败')
      }
    },

    // 提取高亮文本方法
    highlight (item) {
      // 1.设置关键字的高亮文本样式
      const highlightStr = `<span style="color: red">${this.searchText}</span>`
      // 2.通过split方法将目标字符串拆分成数组
      // 3.通过join方法,将highlightStr 拼接到每个元素之间
      return item.split(this.searchText).join(highlightStr)
    }
  }
}
</script>

<style lang="less" scoped>

</style>
