<template>
  <div class="search-history">
    <van-cell title="搜索历史">
      <template #right-icon>
        <!-- template标签，在html结构中可以作为包裹部分的一个总体盒子，这个盒子并不会作为标签显示到真实dom上 -->
        <template v-if="isDeleteShow">
          <span style="margin-right: 4px" @click="clearAll" >全部删除</span>
          <span>完成</span>
        </template>
        <van-icon name="delete" class="delete-icon" v-else @click="isDeleteShow = true" />
      </template>
    </van-cell>

    <van-cell
    v-for="(value,index) in searchHistories"
    :key="index"
    :title="value"
    @click="onClearHistory(value,index)"
    >
      <template #right-icon v-if="isDeleteShow" >
        <van-icon name="close" class="delete-icon" />
      </template>
    </van-cell>

  </div>
</template>

<script>
/*
  谁的业务，写在谁身上, 如果该业务收到其他的功能影响，无法通过常规的思路实现时，尝试把改业务中的某些受到影响的变量，写的影响源（父组件）上
*/
export default {
  name: 'SearchHistory',
  props: {
    searchHistories: {
      type: Array,
      default: () => []
    }
  },
  data () {
    return {
      isDeleteShow: false // 控制删除状态的显示
    }
  },
  methods: {
    // 删除全部
    clearAll () {
      this.$emit('clearAll')
    },

    // 单个删除
    onClearHistory (value, index) {
      // 判断是否删除状态
      if (this.isDeleteShow) {
        // 删除历史
        this.$emit('clearHistory', index)
      } else {
        // 直接进入搜索
        this.$emit('search', value)
      }
    }
  }
}
</script>

<style lang="less" scoped>
.search-history {
  .delete-icon {
    font-size: 28px;
    line-height: 48px;
  }
}
</style>
