<template>
  <div class="search-result">
    <van-list
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    :error.sync="error"
    error-text="请求失败,点击重新加载"
    @load="onload"
    >
    <van-cell v-for="item in list " :key="item.art_id" :title="item.title" />
    </van-list>
  </div>
</template>

<script>
import { getSearchResultAPI } from '../../../api/index.js'
export default {
  name: 'SearchResult',
  props: {
    searchText: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      page: 1, // 当前页码
      perPage: 20, // 每页的请求数量
      list: [],
      loading: false,
      finished: false,
      error: false
    }
  },
  methods: {
    async onload () {
      // 1. 请求获取更新数据
      try {
        const { data: res } = await getSearchResultAPI({
          page: this.page, // 页码
          per_page: this.perPage, // 每页数量
          q: this.searchText
        })
        // 2.将数据添加到列表中
        console.log(res.data.result)
        this.list.push(...res.data.results)

        // 3.设置加载状态的关闭
        this.loading = false

        // 4.判断数据是否完全加载完毕 - finished的状态调整
        if (res.data.results.length) {
          // 更新下一页的页码
          this.page++
        } else {
          // 列表长度为0时,表明没有数据了,那么将finished改为true 不在调用onload
          this.finished = true
        }
      } catch (error) {
        // 展示夹杂失败的提示
        this.error = true
        this.loading = false
        this.$toast('请求失败,请稍后重试')
      }
    }
  }
}
</script>

